import "core-js/modules/es.error.cause.js";
import xllLoading from '@/components/xllLoading';
import meSideMenu from "@/components/meSideMenu";
import { EVENTS_ADD_EVENT, EVENTS_CATEGORY, EVENTS_TAGS, ZOHO_SYNC, UPLOAD_BY_ALI_OSS, UPLOAD_BY_SERVICE, GET_COUNTRY_LIST, USER_OBJECT_LIST } from '@/api/api';
import mapboxgl from "mapbox-gl";
import 'mapbox-gl/dist/mapbox-gl.css';
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
import ImageCompressor from 'compressorjs';
import { decode } from 'js-base64';
export default {
  name: "post",
  components: {
    meSideMenu,
    xllLoading
  },

  setup() {
    const eventDisabledDate = date => {
      let myDate = new Date();
      let year = myDate.getFullYear();
      let month = myDate.getMonth() + 1;
      let day = myDate.getDate();
      let mTime = year + '-' + month + '-' + day;
      let mDate = new Date(mTime);
      return date.getTime() < mDate.getTime();
    };

    return {
      eventDisabledDate
    };
  },

  data() {
    const checkEventDate = (rule, value, callback) => {
      console.log(value);

      if (!value) {
        return callback(new Error('Please select date'));
      }

      if (!this.startTime) {
        return callback(new Error('Please select start time'));
      }

      if (!this.endTime) {
        return callback(new Error('Please select end time'));
      }

      callback();
    };

    return {
      dialogSingleImageVisible: false,
      dialogSingleImageUrl: '',
      currencyList: [],
      addCurrencyStatus: false,
      ownCurrencyValue: '',
      ownCurrencyList: [],
      selectCurrencyList: [],
      selectCurrencyArr: [],
      uploadLoadingStatus: false,
      mapLoading: false,
      map1Loading: false,
      uploadActionUrl: process.env.VUE_APP_UPLOAD_ACTION_URL,
      uploadHeaders: {
        platform: 4
      },
      uploadData: {
        token: localStorage.getItem('token')
      },
      flyerPhotoUrl: '',
      companyLogoPhotoUrl: '',
      accessToken: process.env.VUE_APP_MAP_BOX_ACCESS_TOKEN,
      mapStyle: process.env.VUE_APP_MAP_BOX_STYLE,
      dealLocationTypeValue: 1,
      userInfo: {},
      basicUserInfo: {},
      sLocationType: 1,
      //1 国外 2国内
      countryObj: {},
      provinceObj: {},
      cityObj: {},
      countryName: '',
      countryNameCn: '',
      provinceName: '',
      provinceNameCn: '',
      cityName: '',
      cityNameCn: '',
      countryOptions: [],
      provinceOptions: [],
      cityOptions: [],
      startTime: '',
      endTime: '',
      eventDate: '',
      basicForm: {
        token: localStorage.getItem('token'),
        user_id: localStorage.getItem('uid'),
        name: undefined,
        desc: undefined,
        third_company_logo: undefined,
        third_company_name: undefined,
        type_desc: undefined,
        pay_money: undefined,
        date: undefined,
        file: undefined,
        file_name: undefined,
        is_all: '1',
        event_place: undefined,
        start_time: undefined,
        end_time: undefined,
        is_online: 1,
        online_url: undefined,
        location: undefined,
        country_id: undefined,
        state_id: undefined,
        town_id: undefined,
        lat: undefined,
        lng: undefined,
        category_id: undefined,
        currency: 'USD',
        tag: [],
        tags_cn: '',
        tags_en: ''
      },
      basicRules: {
        name: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }],
        desc: [{
          required: true,
          message: 'Please input',
          trigger: 'blur'
        }],
        date: [{
          required: true,
          validator: checkEventDate,
          trigger: 'blur'
        }],
        file: [{
          required: true,
          message: 'Please upload',
          trigger: 'blur'
        }],
        third_company_logo: [{
          required: false,
          message: 'Please upload',
          trigger: 'blur'
        }],
        third_company_name: [{
          required: false,
          message: 'Please input',
          trigger: 'blur'
        }]
      },
      categoryData: [],
      tagsData: [],
      addTagsStatus: false,
      ownTagsValue: '',
      ownTagsList: [],
      selectTagsList: [],
      selectTagsArr: [],
      tagsCnData: [],
      tagsEnData: [],
      submitLoadingValue: false,
      selectTagsValue: []
    };
  },

  mounted() {
    let str = this.$route.query.str;

    if (str) {
      let editStr = JSON.parse(decode(str));
      console.log(editStr);
      this.basicForm = Object.assign({}, editStr);
      this.basicForm.event_id = editStr.id;
      this.flyerPhotoUrl = editStr.file;
      this.companyLogoPhotoUrl = editStr.third_company_logo;
    }

    this.getAllCountry();
    this.getUserObjectList();
    this.getEventCategories();
    this.getEventsTags();
  },

  methods: {
    handleSingleImagePreview(file) {
      this.dialogSingleImageUrl = file;
      this.dialogSingleImageVisible = true;
    },

    companyLogoHttpRequest(options) {
      let self = this; // console.log(options)

      new ImageCompressor(options.file, {
        quality: 0.6,

        success(file) {
          // console.log(file)
          const formData = new FormData();
          formData.append('token', localStorage.getItem('token')); // console.log(file)

          let isInChina = process.env.VUE_APP_CHINA;

          if (isInChina === 'yes') {
            formData.append('file[]', file, file.name);
            UPLOAD_BY_ALI_OSS(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.data[0]['file_url'];
                self.uploadLoadingStatus = false;
                self.companyLogoPhotoUrl = myFileUrl;
                self.basicForm.third_company_logo = myFileUrl;
              }
            }).catch(err => {
              console.log(err);
            });
          }

          if (isInChina === 'no') {
            formData.append('file', file, file.name);
            UPLOAD_BY_SERVICE(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.message.file_path;
                self.uploadLoadingStatus = false;
                self.companyLogoPhotoUrl = myFileUrl;
                self.basicForm.third_company_logo = myFileUrl;
              }
            }).catch(err => {
              console.log(err);
            });
          }
        },

        error(err) {
          console.log(err.message);
        }

      });
    },

    flyerHttpRequest(options) {
      let self = this; // console.log(options)

      new ImageCompressor(options.file, {
        quality: 0.6,

        success(file) {
          // console.log(file)
          const formData = new FormData();
          formData.append('token', localStorage.getItem('token')); // console.log(file)

          let isInChina = process.env.VUE_APP_CHINA;

          if (isInChina === 'yes') {
            formData.append('file[]', file, file.name);
            UPLOAD_BY_ALI_OSS(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.data[0]['file_url'];
                self.uploadLoadingStatus = false;
                self.flyerPhotoUrl = myFileUrl;
                self.basicForm.file = myFileUrl;
              }
            }).catch(err => {
              console.log(err);
            });
          }

          if (isInChina === 'no') {
            formData.append('file', file, file.name);
            UPLOAD_BY_SERVICE(formData).then(res => {
              // console.log(res)
              if (res.code == 200) {
                let myFileUrl = res.message.file_path;
                self.uploadLoadingStatus = false;
                self.flyerPhotoUrl = myFileUrl;
                self.basicForm.file = myFileUrl;
              }
            }).catch(err => {
              console.log(err);
            });
          }
        },

        error(err) {
          console.log(err.message);
        }

      });
    },

    getEventsTags() {
      let params = {
        page: 1,
        limit: 10000,
        type: 2
      };
      EVENTS_TAGS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.tagsData = res.message.data;
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    getEventCategories() {
      let params = {
        page: 1,
        limit: 10000
      };
      EVENTS_CATEGORY(params).then(res => {
        this.categoryData = res.message.data;
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    dealLocationTypeChange(e) {
      let self = this;
      this.dealLocationTypeValue = e;
      self.basicForm.is_online = e;

      if (e === 2) {
        this.mapLoading = true;
        setTimeout(function () {
          self.initMap();
          self.mapLoading = false;
        }, 1000);
      }

      if (e === 3) {
        this.map1Loading = true;
        setTimeout(function () {
          self.initMap();
          self.map1Loading = false;
        }, 1000);
      }
    },

    beforeFlyerPhotoUpload(file) {
      // this.$loading({
      //   text:'Uploading...'
      // })
      this.uploadLoadingStatus = true;
      const isLt2M = file.size / 1024 / 1024 < 20;

      if (!isLt2M) {
        this.$message.error('Avatar picture size can not exceed 20MB');
      }

      return isLt2M;
    },

    beforeCompanyLogoPhotoUpload(file) {
      this.uploadLoadingStatus = true;
      const isLt2M = file.size / 1024 / 1024 < 20;

      if (!isLt2M) {
        this.$message.error('Avatar picture size can not exceed 20MB');
      }

      return isLt2M;
    },

    cancelUpload() {
      this.uploadLoadingStatus = false;
    },

    eventDateChange(e) {
      // console.log(e)
      this.eventDate = e;
    },

    startTimeChange(e) {
      console.log(e);
      this.startTime = e;
      let a = e + ':00';
      console.log(a);
      let startTime = this.eventDate + ' ' + a;
      console.log(startTime);
    },

    endTimeChange(e) {
      console.log(e);
      this.endTime = e;
    },

    initMap() {
      mapboxgl.accessToken = this.accessToken;
      const map = new mapboxgl.Map({
        container: "mapContainer",
        center: [121.472644, 31.231706],
        style: this.mapStyle,
        zoom: 12
      });
      const nav = new mapboxgl.NavigationControl();
      map.addControl(nav, "top-right");
      const geolocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      });
      map.addControl(geolocate, "top-right");
      const geocoder = new MapboxGeocoder({
        "accessToken": this.accessToken,
        "mapboxgl": mapboxgl
      });
      map.addControl(geocoder, 'top-left');
      const marker = new mapboxgl.Marker(); // {
      //   draggable:true
      // }
      // marker.on('dragend',(e)=>{
      //   console.log(e)
      // })

      geocoder.on('result', e => {
        console.log(e);
        marker.setLngLat(e.result.center).addTo(map);
        this.basicForm.location = e.result.place_name;
        this.basicForm.lng = e.result.center[0];
        this.basicForm.lat = e.result.center[1];
      });
      geocoder.on('clear', e => {
        console.log(e);
        this.basicForm.location = '';
        this.basicForm.lng = '';
        this.basicForm.lat = '';
      });
    },

    initMap1() {
      mapboxgl.accessToken = this.accessToken;
      const map = new mapboxgl.Map({
        container: "mapContainer1",
        center: [121.472644, 31.231706],
        style: this.mapStyle,
        zoom: 12
      });
      const nav = new mapboxgl.NavigationControl();
      map.addControl(nav, "top-right");
      const geolocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      });
      map.addControl(geolocate, "top-right");
      const geocoder = new MapboxGeocoder({
        "accessToken": this.accessToken,
        "mapboxgl": mapboxgl
      });
      map.addControl(geocoder, 'top-left');
      const marker = new mapboxgl.Marker(); // {
      //   draggable:true
      // }
      // marker.on('dragend',(e)=>{
      //   console.log(e)
      // })

      geocoder.on('result', e => {
        console.log(e);
        marker.setLngLat(e.result.center).addTo(map);
        this.basicForm.location = e.result.place_name;
        this.basicForm.lng = e.result.center[0];
        this.basicForm.lat = e.result.center[1];
      });
      geocoder.on('clear', e => {
        console.log(e);
        this.basicForm.location = '';
        this.basicForm.lng = '';
        this.basicForm.lat = '';
      });
    },

    getAllCountry() {
      let params = {};
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.countryOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    getAllProvinces(countryId) {
      let params = {
        country_id: countryId
      };
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.provinceOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    getAllCitys(countryId, stateId) {
      let params = {
        country_id: countryId,
        state_id: stateId
      };
      GET_COUNTRY_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.cityOptions = res.message;
        }
      }).catch(err => {
        this.$message.error(err.msg);
      });
    },

    countryChange(e) {
      console.log(e);
      this.basicForm.state_id = undefined;
      this.basicForm.town_id = undefined;
      this.provinceOptions = [];
      this.cityOptions = [];
      this.basicForm.country_id = e.id;
      this.countryName = e.name;
      this.countryNameCn = e.name;
      this.getAllProvinces(e.id);
    },

    provinceChange(e) {
      console.log(e);
      this.basicForm.town_id = undefined;
      this.cityOptions = [];
      this.basicForm.state_id = e.id;
      this.provinceName = e.name;
      this.provinceNameCn = e.name;
      this.getAllCitys(this.basicForm.country_id, e.id);
    },

    cityChange(e) {
      console.log(e);
      this.basicForm.town_id = e.id;
      this.cityName = e.name;
      this.cityNameCn = e.name;
    },

    getUserObjectList() {
      let data = {
        token: localStorage.getItem('token')
      };
      USER_OBJECT_LIST(data).then(res => {
        if (res.code == 200) {
          this.currencyList = res.message.filter(item => item.pid === 117); // currency
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    discard() {
      this.$router.go(-1);
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let tagsValue = this.selectTagsValue;
          let tagsIdData = [];
          let tagsNameEn = [];
          let tagsNameCn = [];

          if (tagsValue.length > 0) {
            tagsValue.forEach(item => {
              if (typeof item === 'string') {
                console.log('string');
                tagsNameCn.push(item);
                tagsNameEn.push(item);
              }

              if (typeof item === 'object') {
                console.log('object');
                tagsIdData.push(item.id);
                tagsNameCn.push(item.name_cn);
                tagsNameEn.push(item.name_en);
              }
            });
          } // console.log(this.selectCurrencyList)


          this.basicForm.tag = tagsIdData;
          this.basicForm.tags_cn = tagsNameCn.join(',');
          this.basicForm.tags_en = tagsNameEn.join(',');

          if (this.eventDate) {
            this.basicForm.date = this.eventDate;
          }

          if (this.eventDate && this.startTime) {
            this.basicForm.start_time = this.eventDate + ' ' + this.startTime + ':00';
          }

          if (this.eventDate && this.endTime) {
            this.basicForm.end_time = this.eventDate + ' ' + this.endTime + ':00';
          }

          let countryObj = {
            country_name_en: this.countryName,
            country_name_cn: this.countryNameCn,
            province_name_en: this.provinceName,
            province_name_cn: this.provinceNameCn,
            city_name_en: this.cityName,
            city_name_cn: this.cityNameCn
          };
          this.basicForm.country_info = JSON.stringify(countryObj);
          this.$loading({
            text: 'Loading...'
          });
          let params = Object.assign({}, this.basicForm);
          EVENTS_ADD_EVENT(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              // this.submitEventForm()
              this.$loading().close();
              this.$router.push('/events/myEvents');
            }
          }).catch(err => {
            console.log(err);
            this.$message.error(err.msg);
            this.$loading().close();
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    async submitEventForm() {
      let params = Object.assign({}, this.basicForm);
      let startTimeStr = this.eventDate + ' ' + this.startTime + ':00';
      let endTimeStr = this.eventDate + ' ' + this.endTime + ':00';
      let startTimeDate = new Date(startTimeStr);
      let startHours = startTimeDate.getHours();
      let startMinutes = startTimeDate.getMinutes();
      let startTimeampm = startHours >= 12 ? 'PM' : 'AM';
      startHours = startHours % 12;
      startHours = startHours ? startHours : 12;
      startHours = startHours.toString().padStart(2, '0');
      startMinutes = startMinutes.toString().padStart(2, '0');
      let endTimeDate = new Date(endTimeStr);
      let endHours = endTimeDate.getHours();
      let endMinutes = endTimeDate.getMinutes();
      let endTimeampm = endHours >= 12 ? 'PM' : 'AM';
      endHours = endHours % 12;
      endHours = endHours ? endHours : 12;
      endHours = endHours.toString().padStart(2, '0');
      endMinutes = endMinutes.toString().padStart(2, '0');
      let zohoData = [{
        'zf_referrer_name': ''
      }, {
        'zf_redirect_url': ''
      }, {
        'zc_gad': ''
      }, {
        'SingleLine': params.name //event name//

      }, {
        'Dropdown2': params.category_id // event category

      }, {
        'SingleLine1': params.pay_money //  event price

      }, {
        'SingleLine3': '' //  event contact

      }, {
        'SingleLine4': '' //   Organizing Company

      }, {
        'Dropdown': params.tags_en //   event tags

      }, {
        'Date': this.eventDate //   event date

      }, {
        'Time_hours': startHours //   event start time

      }, {
        'Time_minutes': startMinutes //   event start time

      }, {
        'Time_meridiem': startTimeampm //   am pm

      }, {
        'Time1_hours': endHours //  event end time

      }, {
        'Time1_minutes': endMinutes //   event end time

      }, {
        'Time1_meridiem': endTimeampm //  am pm

      }, {
        'Email': localStorage.getItem('email') //  contact email

      }, {
        'SingleLine6': params.event_place //  event venue

      }, {
        'SingleLine7': params.location //  event street address

      }, {
        'Website': params.file //  poster url

      }, {
        'Dropdown1': '' //   event post status

      }, {
        'MultiLine': params.desc //  event description

      }, {
        'MultiLine1': params.type_desc //  deals for memebers

      }];
      let zohoParams = {
        zoho_data: zohoData,
        zoho_url: 'https://forms.zohopublic.com/edupassport/form/PostEventform/formperma/Far3vsluPJX_E1n27v0883C-insXpT_m6rJsJz-L5r8/htmlRecords/submit'
      };
      await ZOHO_SYNC(zohoParams).then(res => {
        console.log(res);
      }).catch(err => {
        console.log(err);
      });
    }

  }
};